* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: rgb(171, 171, 240);
  color: #222;
}
.booklist {
  display: grid;
  width: 90vw;
  max-width: 1170px;
  margin: 5rem auto;
  gap: 2rem;
}
@media screen and (min-width: 768px) {
  .booklist {
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
  }
}
.book {
  background-color: rgb(174, 98, 245);
  border-radius: 1rem;
  padding: 1rem 2rem;
}
.book h1 {
  margin-top: 0.5rem;
}
.book h4 {
  color: #b11226;
  font-weight: bold;
  font-size: 0.9rem;
  margin-top: 0.9rem;
}
img {
  width: 20rem;
  height: 25rem;
}
.book p {
  text-align: justify;
  font-size: 0.8rem;
  margin-top: 0.5rem;
}
.center {
  display: flex;
  justify-content: center;
  align-items: center;
}
button {
  background-color: rgb(114, 55, 196);
  font-size: 1.3rem;
  padding: 0.5rem 1rem;
  border-radius: 15%;
  font-weight: bold;
  margin-top: 0.8rem;
  color: white;
}
